.projectCard{
    background:hsla(112, 41%, 85%, 0.15);
    margin:0 auto;
    display:flex;
    margin-top:10px; 
}
.projectCardContent{
    padding-bottom:25px;
    padding-left:10px;
    padding-right:10px;
}
.projectCardContent span{
    margin-top:25px;
    padding-bottom:0px;
}

.projectCardContent span a{
    text-decoration: none;
    color:white;
    background:hsl(112, 41%, 46%);
    padding:5px;
    margin-right:5px;
    transition:.3s ease background, .3s ease filter;
}

.projectCardContent span a:hover{
    background:hsl(112, 41%, 56%);
    filter:drop-shadow(0px 2px 2px hsl(112, 41%, 26%));
}

.projectCardContent p{
    margin:0;
    text-align:justify;
    max-width:75ch;
    background:none;
}
