@import url(https://fonts.googleapis.com/css2?family=Inconsolata&family=Palanquin&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
  /*
  background:url("./res/mitchell-luo-li7LkjxlB40-unsplash.jpg");
  */
  background-color: hsl(112, 41%, 96%);
  background-image: url("https://www.transparenttextures.com/patterns/brushed-alum.png");
  /* This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! */
  font-size:24px;
  padding-bottom:25px;
}
@media(max-width:600px){
  html{
    font-size:12px;
  }
}
html:before{
  content:"";
  position:fixed;
  z-index:-1;
  width:100vw;
  height:100vh;
  background:rgba(0,0,0,0.05);
}
.App {
  text-align: center;
  display:flex;
  flex-direction:row;
}






.frontButton{
    text-decoration:none;
    color:black;
    background:none;
    transition:.2s ease all;
    font-size:1.5rem;
    z-index:2;
    position:absolute;
    width:100%;
    top:0;
    left:0;
}
.frontButtonWrapper{
    position:relative;
    padding:5px;
    width:12rem;
    height:1.5rem;
}
.frontButtonWrapper span{
    position:absolute;
    top:0;
    left:0;
    background:hsl(112, 41%, 56%);
    height:100%;
    transition:.3s ease all;
    z-index:1;
}
.frontButtonWrapper:hover{
    -webkit-filter:drop-shadow(0px 2px 2px hsl(112, 41%, 36%));
            filter:drop-shadow(0px 2px 2px hsl(112, 41%, 36%));
}


.frontButton:hover{
    -webkit-filter:drop-shadow(0px 0px 2px hsl(112, 41%, 26%));
            filter:drop-shadow(0px 0px 2px hsl(112, 41%, 26%));
    color:hsl(112, 41%, 86%);

}

@media(max-width:600px)
{
    .frontButtonWrapper{
       width:8rem; 
    }
    
}
@keyframes rotateLogo{
    0%{
        transform: rotateZ(0);
        border-radius: 95% 50% 95% 50%;
    }
    50%{
        border-radius: 90% 55% 65% 55%;
    }
    100%{
        transform: rotateZ(180deg);
        border-radius: 95% 50% 95% 50%;
    }
}

@keyframes rotateBanner1{
    0%{
        top:0px;
        transform: rotateX(-90deg);
    }
    5%{
        top:15px;
        transform: rotateX(0deg);
    }
    100%{
        top:15px;
        transform: rotateX(0deg);
    }
}
@keyframes rotateBanner2{
    0%{
        top:-25px;
        transform: rotateX(0deg);
    }
    5%{
        top:-10px;
        transform: rotateX(90deg);
    }
    100%{
        top:-10px;
        transform: rotateX(90deg);
    }
}

.head{
    margin:0 auto;
    margin-top:40px;
    display:flex;
    flex-direction:column;
   
}
.menuWrapper{

    display:flex;
    margin:0 auto;
    margin-top:150px
}
.headBanner{
    margin:0;
    margin:0 auto;
    position:relative;
    width:200px;
    -webkit-filter: drop-shadow(0px 10px 4px darkgray);
            filter: drop-shadow(0px 10px 4px darkgray);
}

#headShape{
    display:block;
    position:relative;
    top:0;
    width:200px;
    height:200px;
    border-radius: 95% 50% 95% 50%;
    background:#6DBD60;
    z-index:1;
    animation-name:rotateLogo;
    animation-duration:10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

.headBanner h3 h2{
    position:relative;
    z-index:2;
    margin:0;
}
.headBanner h3{
    font-size:1.5rem;
    margin:0;    
    font-family:Inconsolata;
    padding:0;
    position:relative;
}
.headBanner h3:nth-of-type(1)
{
    animation-name:rotateBanner1;
    animation-duration:5s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
}

.headBanner h3:nth-of-type(2)
{
    top:-25px;
    animation-name:rotateBanner2;
    animation-duration:5s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
}

.headBanner h2{
    font-size:2rem;
    margin:0;
    font-family:Palanquin;

}

h3{
    font-family:Inconsolata;
    font-size:1.25rem;
}

#headTitle{
    /*text-shadow:0px 2px 3px hsl(0,0%,55%);*/
    margin-top:50px;
    width:600px;
    position:absolute;
    top:0;
    left:51%;
    display:block;
    text-align:left;
    z-index:3;
}
p{
    max-width: 75ch;
    text-align:left;
    text-indent: 5ch;
    margin: 0 auto;
    margin-top:40px;
    font-family: Inconsolata;
}
.sectionTitle{
    background: #6DBD60;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    margin: 0 auto;
    margin-top: 40px;
    padding-left:15px;
    padding-right:15px;
}

#aboutMe{
    margin-top:80px;
    display:flex;
    flex-direction:row;
    background:hsla(112, 41%, 85%, 0.15);
}

#aboutMe h3{
    font-size:2rem;
    padding-left:50px;
    padding-right:50px;
    margin-top:0;
}
#aboutMe p{
    margin:0;
}

.icon-github, .icon-linkedin{
    color:#6DBD60;
    text-decoration:none;
    transition:.2s ease all;
    font-size:2rem;
    margin-right:30px;
    text-decoration:none;
}


.icon-github::selection, .icon-linkedin::selection{
    color:#6DBD60;
}
.icon-github:hover, .icon-linkedin:hover{
    color:hsl(112, 41%, 66%);
    
    -webkit-filter:drop-shadow(0px 2px 2px black);
    
            filter:drop-shadow(0px 2px 2px black);
}
.icon-github:focus, .icon-linkedin:focus{
    color:hsl(112, 41%, 46%);
}

@media(max-width:600px)
{
    #headShape{
        width:100px;
        height:100px;
    }
    .headBanner{
        width:100px;
    }

    #headTitle{
        width:200px;
    }
    .menuWrapper{
        margin-top:100px;
    }
}

#contentContainer{
    width:50vw;
    min-width:400px;
    margin:0 auto;
}

#contact{
    margin:0;
    background:hsla(112, 41%, 85%, 0.15);
}
